<template>
  <div class="load">
    <a-spin :spinning="detailFormState.spinning" tip="Loading...">
      <div class="process-box">
        <!-- 标题 -->
        <div class="header-box">
          <BillTitle :options="billTitleOption" />
          <WfApproveBox
            v-if="detailFormState.status != 10"
            @save="onSave"
            @submit="onSubmit"
            :processInstanceId="detailFormState.processInstanceId"
            :processStatus="detailFormState.status"
          />
        </div>
        <!-- 表单内容 -->
        <div class="content">
          <a-form ref="formRef" :model="detailFormState" labelAlign="right" autocomplete="off">
            <a-card title="基本信息" :bordered="false">
              <a-row :gutter="24">
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="stationName"
                    label="电站名称"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请输入电站名称' }]"
                  >
                    <a-input
                      disabled
                      v-model:value="detailFormState.stationName"
                      placeholder="请输入电站名称"
                    />
                    <a-input disabled v-model:value="detailFormState.id" v-if="false" />
                  </a-form-item>
                </a-col>
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="periodName"
                    label="期数"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请输入期数' }]"
                  >
                    <a-input
                      disabled
                      v-model:value="detailFormState.periodName"
                      placeholder="请输入期数"
                    />
                  </a-form-item>
                </a-col>
              </a-row>

              <a-row :gutter="24">
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="contractDate"
                    label="合同日期"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请选择合同日期' }]"
                  >
                    <a-date-picker
                      v-model:value="detailFormState.contractDate"
                      format="YYYY-MM-DD"
                      value-format="YYYY-MM-DD"
                      style="width: 100%"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="contractType"
                    label="合同类型"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请选择合同类型' }]"
                  >
                    <DictSelectBox
                      :type="`OaContractType`"
                      @change="`()=> detailFormState.contractType = value`"
                      v-model:value="detailFormState.contractType"
                      style="width: 100%"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
              </a-row>

              <a-row :gutter="24">
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="contractName"
                    label="合同名称"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请输入合同名称' }]"
                  >
                    <a-input
                      v-model:value="detailFormState.contractName"
                      placeholder="请输入合同名称"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="contractAmount"
                    label="合同金额(元)"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: false, message: '请输入合同金额' }]"
                  >
                    <a-input-number
                      v-model:value="detailFormState.contractAmount"
                      placeholder="请输入合同金额"
                      precision="2"
                      max="1000000000"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
              </a-row>

              <a-row :gutter="24">
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="partyA"
                    label="甲方"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请输入甲方' }]"
                  >
                    <a-input
                      v-model:value="detailFormState.partyA"
                      placeholder="请输入甲方"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="partyB"
                    label="乙方"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请输入乙方' }]"
                  >
                    <a-input
                      v-model:value="detailFormState.partyB"
                      placeholder="请输入乙方"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
              </a-row>

              <a-row :gutter="24">
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="startDate"
                    label="服务开始日期"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请选择服务开始日期' }]"
                  >
                    <a-date-picker
                      v-model:value="detailFormState.startDate"
                      format="YYYY-MM-DD"
                      value-format="YYYY-MM-DD"
                      style="width: 100%"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="endDate"
                    label="服务结束日期"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请选择服务结束日期' }]"
                  >
                    <a-date-picker
                      v-model:value="detailFormState.endDate"
                      format="YYYY-MM-DD"
                      value-format="YYYY-MM-DD"
                      style="width: 100%"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
              </a-row>

              <a-row :gutter="24">
                <a-col v-show="true" :span="24">
                  <a-form-item
                    name="remark"
                    label="备注"
                    :labelCol="{ span: 3 }"
                    :rules="[{ required: false, message: '请输入备注' }]"
                  >
                    <a-textarea
                      v-model:value="detailFormState.remark"
                      row="8"
                      show-count
                      style="height: 200px"
                      :disabled="disabledInput"
                      :maxlength="1000"
                    />
                  </a-form-item>
                </a-col>
              </a-row>
            </a-card>

            <a-card :bordered="false">
              <BasicTable title="主要内容" @register="registerTaskTable">
                <template #toolbar>
                  <a-button
                    v-if="!disabledInput"
                    type="primary"
                    :preIcon="IconEnum.ADD"
                    @click="handleAddItem()"
                  >
                    {{ t('common.action.create') }}
                  </a-button>
                </template>
                <template #bodyCell="{ column, record }">
                  <template v-if="column.dataIndex === 'contractKind'">
                    <a-input
                      v-model:value="record[column.dataIndex]"
                      :maxLength="1000"
                      :disabled="disabledInput"
                    />
                  </template>

                  <template v-if="column.dataIndex === 'contractContent'">
                    <a-input
                      v-model:value="record[column.dataIndex]"
                      :maxLength="1000"
                      :disabled="disabledInput"
                    />
                  </template>

                  <template v-if="column.dataIndex === 'action'">
                    <TableAction
                      :actions="[
                        {
                          tooltip: '删除',
                          color: 'error',
                          icon: 'ant-design:delete-outlined',
                          ifShow: !disabledInput,
                          onClick: handleDeleteItem.bind(null, record),
                        },
                      ]"
                    />
                  </template>
                </template>
              </BasicTable>
            </a-card>

            <a-card title="附件信息" :bordered="false">
              <div class="clearfix" style="padding-bottom: 15px; padding-left: 15px">
                <UploadBox
                  :width="800"
                  :height="550"
                  :maxCount="20"
                  :maxSize="100 * 1024 * 1024"
                  :application="`po`"
                  :module="`contract`"
                  :vmode="detailFormState.status == 0 ? `box` : `view`"
                  :bizId="detailFormState.bizFileId"
                />
              </div>
            </a-card>
          </a-form>
        </div>
      </div>
    </a-spin>
  </div>
</template>

<script lang="ts" setup>
  import { reactive, ref, onMounted, unref } from 'vue';
  import {
    saveData,
    initInfoItems,
    getIndex,
    getDetailList,
    detailColumns,
    detailTableProps,
  } from './contract';
  import { FormInstance, SelectProps, message } from 'ant-design-vue';
  import { useRouter } from 'vue-router';
  import WfApproveBox from '/@/components/Framework/WorkFlow/WfApproveBox.vue';
  import { assign } from 'lodash-es';
  import * as ProcessInstanceApi from '@/api/bpm/processInstance';
  import BillTitle from '/@/components/Framework/BillTitle/BillTitle.vue';
  import { BasicTable, useTable, TableAction, EditRecordRow } from '/@/components/Table';
  import type { UploadProps, TreeSelectProps } from 'ant-design-vue';
  import DictSelectBox from '@/components/Framework/Combox/DictSelectBox.vue';
  import { IconEnum } from '@/enums/appEnum';
  import { useI18n } from '@/hooks/web/useI18n';
  import * as FileApi from '@/api/infra/file';
  import UploadBox from '@/components/Framework/Combox/UploadBox.vue';
  import { closeCurrentTab } from '@/utils/route';
  import { MsgManager } from '/@/message/MsgManager';
  import { useUserStore } from '/@/store/modules/user';
  import { Memory } from '@/router/index';

  const { t } = useI18n();

  const detailFormState = reactive({ spinning: false });
  const userStore = useUserStore();
  const getUserInfo = userStore.getUserInfo;
  const detailDataSource = ref<any[]>();

  const disabledInput = ref<boolean>(true);
  const billTitleOption = reactive<any>({});
  billTitleOption.subject = '合同信息';
  billTitleOption.infoItems = [];
  const router = useRouter();
  const { currentRoute } = router;
  const route = unref(currentRoute);
  const query = Memory.getInstance().getQuery(); // route.query;

  const formRef = ref<FormInstance>();

  function dig(data) {
    const list: TreeSelectProps['treeData'] = [];
    for (let i = 0; i < data.length; i++) {
      const item = data[i];
      const treeNode: TreeSelectProps['treeData'][number] = {
        label: item.name,
        value: item.id,
        disabled: item.orgKindId !== 'psm',
      };

      if (item.children && item.children.length > 0) {
        treeNode.children = dig(item.children);
      }

      list.push(treeNode);
    }
    return list;
  }

  function save(status) {
    const dtlList = ref<any>([]);
    getTaskDataSource().forEach((data, index) => {
      const dtl = ref<any>({});
      assign(dtl.value, data);
      dtl.value.sort = index + 1;
      dtlList.value.push(dtl.value);
    });

    if (dtlList.value.length == 0) {
      message.error('请添加至少一条主要内容明细。');
      return;
    }

    detailFormState.dtlList = dtlList.value;
    detailFormState.spinning = true;
    try {
      status = status || 0;

      formRef.value.validateFields().then(() => {
        detailFormState.status = status;
        saveData(detailFormState).then((data) => {
          detailFormState.id = data;
          getIndex({ id: data }).then((data) => {
            assign(detailFormState, data.result);
          });
          message.success(status == 1 ? '提交成功。' : '保存成功');
          MsgManager.getInstance().sendMsg('integrated-contract', {});
          if (status == 1) {
            disabledInput.value = true;
            closeCurrentTab();
          }
        });
      });
      //   // router.push('/po/elec/settlementelec/contract');
    } catch (error) {
      console.log('error', error);
    } finally {
      detailFormState.spinning = false;
    }
  }

  const [registerTaskTable, { getDataSource: getTaskDataSource }] = useTable(
    assign(
      {
        dataSource: detailDataSource,
        columns: detailColumns,
      },
      detailTableProps,
    ),
  );

  const initTableData = async () => {
    detailDataSource.value = await getTaskData({});
  };

  const getTaskData = async (paramObj) => {
    const param = await getDetailQueryParam(paramObj);

    const dataObj = await getDetailList(param);

    return dataObj.result;
  };

  const getDetailQueryParam = async (paramObj) => {
    const id = await getBizParamId();
    return { contractId: paramObj.contractId ? paramObj.id : id + '' };
  };

  function handleAddItem() {
    const addRow: EditRecordRow = {};
    pushItemRow(addRow);
  }

  function pushItemRow(addRow: EditRecordRow) {
    const data = getTaskDataSource();
    data.push(addRowOption(addRow));
  }

  function addRowOption(addRow: EditRecordRow) {
    addRow.editable = true;
    addRow.isNew = true;
    addRow.key = `${Date.now()}`;
    // addRow.roleName = '';
    // addRow.innerPersonNames = '';
    // addRow.outerPersonNames = '';
    // addRow.count = '';
    return addRow;
  }

  function handleDeleteItem(record) {
    const data = getTaskDataSource();
    let index = data.indexOf(record);
    if (index !== -1) {
      data.splice(index, 1);
    }
  }

  function handleDeleteFile(record) {
    console.log('handleDeleteFile', record);
  }
  function onSubmit() {
    save(1);
  }

  function onSave() {
    save(0);
  }
  assign(detailFormState, {
    processInstanceId: '',
    id: '',
    billCode: '',
    fillinDate: '',
    organId: '',
    organName: '',
    deptId: '',
    deptName: '',
    positionId: '',
    positionName: '',
    personMemberId: '',
    personMemberName: '',
    fullId: '',
    status: 0,
    bizFileId: '',
    subject: '',
    finishedDate: '',
    contractDate: '',
    contractType: '',
    contractName: '',
    contractAmount: '',
    partyA: '',
    partyB: '',
    startDate: '',
    endDate: '',
    remark: '',
  });
  onMounted(async () => {
    query.id = await getBizParamId();

    await getIndex({ id: query.id }).then((data) => {
      if (query.id) {
        assign(detailFormState, data.result);
        if (
          detailFormState.status == 0 &&
          detailFormState.personMemberId.substring(0, 32) != getUserInfo.userId
        ) {
          detailFormState.status = 10;
        }
      } else {
        assign(detailFormState, query);
        detailFormState.spinning = false;
        detailFormState.status = 0;
        detailFormState.billCode = data.result.billCode;
        detailFormState.personMemberName = data.result.personMemberName;
        detailFormState.fillinDate = data.result.fillinDate;
        detailFormState.deptName = data.result.deptName;
      }

      if (detailFormState.status == 1) {
        disabledInput.value = true;
      }
    });

    if (!query.id) {
      disabledInput.value = false;
    }
    await getRecordable();

    initTableData();

    if (
      detailFormState.bizFileId == null ||
      detailFormState.bizFileId == undefined ||
      detailFormState.bizFileId == ''
    ) {
      getBizFileId();
    }
  });

  //==================================附件资料  begin ==================================

  const getBizFileId = async () => {
    FileApi.getBizId().then((res) => {
      detailFormState.bizFileId = res;
    });
  };

  //==================================附件资料  end ==================================

  const getBizParamId = async () => {
    let id = query.id;
    if (!id && query.processInstanceId) {
      const processInstance = await ProcessInstanceApi.getProcessInstance(
        query.processInstanceId as string,
      );
      const businessKey = processInstance.businessKey;
      query.id = businessKey;
      id = businessKey;
    }
    return id;
  };

  const getRecordable = async () => {
    disabledInput.value = detailFormState.status != 0;
    billTitleOption.title = '合同信息';
    detailFormState.processInstanceId = query.processInstanceId;
    let { billCode, fillinDate, personMemberName, deptName } = detailFormState;
    assign(billTitleOption, { billCode, fillinDate, personMemberName, deptName });
  };
</script>
<style lang="less" scoped></style>
